.forecast{
    min-height: 10vh;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.forecast-item{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 2vh;
    padding: 2vh;
    text-align: center;
}